body {
    background-color: black;
}
.container {
    display: inline-block;
}
h1 {
    font-size: 50px;
    font-weight: normal;
    text-align: center;
    font-style: italic;
    font-family: "Copperplate", Fantasy;
    color: #FFC04B;
    width: 0;
    animation: typing 1s steps(20, end) forwards, blink .8s infinite;
}
@keyframes typing {
    from {
        width: 0
    }

    to {
        width: 100%
    }
}
@keyframes blink {
    from {
        border-color: transparent
    }

    to {
        border-color: darkgreen;
    }
}
